body {
  margin: 0 auto;
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
}

.header {
  background-color: #fff;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header__image {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.header__navigation {
  display: flex;
}

.nav {
  margin-right: 50px;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  text-align: center;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.main {
  grid-template-columns: repeat(auto-fill, 200px);
  justify-content: center;
  gap: 46px 48px;
  margin: auto;
  padding: 50px 40px;
  display: grid;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__product_name {
  color: #060b35;
  width: 166px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card:hover .card__product_name {
  color: #34568b;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__product_box {
  flex-direction: row;
  justify-content: flex-start;
  width: 166px;
  margin-bottom: 16px;
  display: flex;
}

.card__product_code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.card__stars {
  display: flex;
}

.card__reviews_amount {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin-bottom: 16px;
  display: flex;
}

.card__price_word {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price_value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  transition: all .3s;
  display: flex;
}

.card__button_link {
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: color .3s;
}

.card__button:hover {
  background-color: #fff;
}

.card__button:hover .card__button_link {
  color: #00acdc;
}

.stars {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin: 0;
}

.stars--5 .stars__star, .stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

